	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Munich Calling","date":"11 Sep 2002","slug":"/2002-09-11-munich-calling/","tags":"Y2002"},"html":"<p>Mama called. She's catching the last flight from Munich to Hamburg, and won't be home until close to midnight. She said the airport was much emptier than usual. Christopher tried to distract her  by describing our visit with his granparents to the <a href=\"http://www.serengeti-park.com/\">Serengeti Park</a> yesterday. 'Monkey bus. Giraffe. Tiger. Lion. Train. Boat. Elephant. Airplane. Pizza.' Wait, pizza is what we had for supper tonight. And 'monkey bus' means the monkeys crawled all over the moving tour bus, reaching in the windows for fruit that the driver had passed around to the passengers.</p>"},"next":{"fields":{"title":"Dewey Defeats Truman","date":"11 Sep 2002","slug":"/2002-09-11-dewey-defeats-truman/"}},"previous":{"fields":{"title":"No, I Fling Her","date":"13 Sep 2002","slug":"/2002-09-13-no-i-fling-her/"}}},{"node":{"fields":{"title":"Dewey Defeats Truman","date":"11 Sep 2002","slug":"/2002-09-11-dewey-defeats-truman/","tags":"Y2002"},"html":"<p><a href=\"http://scriptingnews.userland.com/backissues/2002/09/10#When:9:50:57AM\">Smart</a> <a href=\"http://www.joelonsoftware.com/news/20020910.html\">people</a> who should know better are quoting the <a href=\"http://websidestory.com/cgi-bin/wss.cgi?corporate&amp;news&amp;press_1_193\">report</a> that \"Internet Explorer... now has a global usage share of 96 percent\". Apart from the fact that the number is ridiculous on its face (how many users are on platforms for which IE is not available?), if you scroll down to the small print, you can read that \"StatMarket publishes information gathered from... more than 125,000 sites actively using WebSideStory?s HitBox\", which is everything other than a random sample. A very large grain of salt is recommended.</p>"},"next":{"fields":{"title":"365 Days Ago","date":"11 Sep 2002","slug":"/2002-09-11-365-days-ago/"}},"previous":{"fields":{"title":"Munich Calling","date":"11 Sep 2002","slug":"/2002-09-11-munich-calling/"}}},{"node":{"fields":{"title":"365 Days Ago","date":"11 Sep 2002","slug":"/2002-09-11-365-days-ago/","tags":"Y2002"},"html":"<p><a href=\"http://www.papascott.de/2001/09/12/1540.php#001540\">\"Perhaps she had started to fathom what has happened. I haven't. Not yet.\"</a></p>"},"next":{"fields":{"title":"Online Registration","date":"10 Sep 2002","slug":"/2002-09-10-online-registration/"}},"previous":{"fields":{"title":"Dewey Defeats Truman","date":"11 Sep 2002","slug":"/2002-09-11-dewey-defeats-truman/"}}}],"pathPrefix":"","first":false,"last":false,"index":848,"pageCount":1101}}

